import React, { Component } from 'react'
import axios from "axios";
import ReactPlayer from 'react-player/youtube'
export default class Play_list extends Component {

    constructor(props) {
      super(props)
    
      this.state = {
        pl_list:"PLwGdqUZWnOp354xMD8u0hxX-1qmCvfLiY",
        ch_list:"UCvjXo25nY-WMCTEXZZb0xsw",
        s_list:"youtube",
        max_result:"6",
        list_resul:[],
        v_id:"aFwZgth790Q",
        chanel:"false"
      }
    }




 Search_pl = (e)=>{
  e.preventDefault();
  const api_key = "AIzaSyCUa4lcV98xTUWnAAufeoeWVm4g49Jr-Ic";
  var f_pl_url = `https://youtube.googleapis.com/youtube/v3/playlistItems?part=snippet&maxResults=${this.state.max_result}&playlistId=${this.state.pl_list}&key=${api_key}`;
  axios.get(f_pl_url).then((res) => { this.setState({list_resul:res.data.items.map((obj,i)=> <div className=' bg_theme mt-2 pl-2 pr-2 yt_line' onClick={(e)=>{e.preventDefault(); this.setState({v_id:obj.snippet.resourceId.videoId})}}  key={i}> <b> {i+1} </b> . {obj.snippet.title}</div> )  })  });
  this.setState({chanel:"false"})
  console.log(f_pl_url);
  console.log(this.state.list_resul)
 } 

 Search_ch = (e)=>{
  e.preventDefault();
  const api_key="AIzaSyCUa4lcV98xTUWnAAufeoeWVm4g49Jr-Ic";
    const f_url = ` https://youtube.googleapis.com/youtube/v3/playlists?part=snippet&maxResults=${this.state.max_result}&channelId=${this.state.ch_list}&key=${api_key}`;
    axios.get(f_url).then((res) => { this.setState({list_resul:res.data.items.map((obj,i)=> <div className=' bg_theme mt-2 pl-2 pr-2 yt_line' onClick={(e)=>{e.preventDefault(); this.setState({pl_list:obj.id})}}  key={i}> <b> {i+1} </b> . {obj.snippet.title}</div> )  })  });
    this.setState({chanel:"true"})
    console.log(f_url)
    console.log(this.state.list_resul)
 }
 Search_s = (e)=>{
  e.preventDefault();
  const api_key="AIzaSyCUa4lcV98xTUWnAAufeoeWVm4g49Jr-Ic";
  const f_url = `https://youtube.googleapis.com/youtube/v3/search?part=snippet&maxResults=${this.state.max_result}&q=${this.state.s_list}&key=${api_key}`;
  axios.get(f_url).then((res) => { this.setState({list_resul:res.data.items.map((obj,i)=> <div className=' bg_theme mt-2 pl-2 pr-2 yt_line' onClick={(e)=>{e.preventDefault(); this.setState({v_id:obj.id.videoId})}}  key={i}> <b> {i+1} </b> . {obj.snippet.title}</div> )  })  });
  this.setState({chanel:"false"})
  console.log(this.state.list_resul)
  console.log(f_url)


}

  render() {



    const v_url =`https://www.youtube.com/watch?v=${this.state.v_id}` ; 
    return ( <div>



      <form className='yt_form'>
        <div className="form-row">
          <div className="col-md-4 mb-12">
            <span className='yt_p '> select how many items you want to show  </span>
            <label >Max result (Number:)</label>
            <input type="text" className="form-control valid" id="validationServer01" placeholder="First name" onChange={(e) => { this.setState({ max_result: e.target.value }) }} value={this.state.max_result} />
          </div>
         
           </div>
           <br /> <p className='text-success '> There is some default value for each search option so that you can test out the thing quite easy  </p>
        <div className="form-row">
        <div className="col-md-4 mb-12">
            <label >Search Playlist by Chanel Id</label>
            <input type="text" className="form-control valid" id="validationServer01" placeholder="First name" onChange={(e) => { this.setState({ ch_list: e.target.value }) }} value={this.state.ch_list}  />
            <button className="btn btn-primary mt-3 f_left" onClick={this.Search_ch} type="submit">Search Playlist </button>
          </div>
        
          <div className="col-md-4 mb-12">
            <label > Search by Play list Id</label>
            <input type="text" className="form-control valid" id="validationServer01" placeholder="First name" onChange={(e) => { this.setState({ pl_list: e.target.value }) }} value={this.state.pl_list}  />
            <button className="btn btn-primary mt-3 f_left" onClick={this.Search_pl} type="submit">Search Videos</button>
          </div>
          <div className="col-md-4 mb-12">
            <label >Search by Text </label>
            <input type="text" className="form-control valid" id="validationServer01" placeholder="First name" onChange={(e) => { this.setState({ s_list: e.target.value }) }} value={this.state.s_list}  />
            <button className="btn btn-primary mt-3 f_left" onClick={this.Search_s} type="submit">Search</button>
          </div>
         
        </div>
      </form>

{/* //////////////////////////////////////////////////////////////////////////////////////////////// */}






<br />

<div className="row">

<div className="col-sm-12 col-md-6 col-12">
<h3 className='text-center mb-3'> Video player  </h3>
<ReactPlayer  url={v_url} controls={true} height="200px" width="400px" />
</div>
<div className="col-sm-12 col-md-6 text-left pl-2 col-12 ">
  {(this.state.chanel==="true")?<div> <h3 className='text-center'> Chanel's Play list  </h3> <p className='yt_p'> Now select a Playlist and  click the   <b className='text-danger font-bolder'> Playlist Search  Button </b>  to get the video list from the selected playlist </p> </div> : <h3 className='text-center'> Video list  </h3>  }


{this.state.list_resul} 
</div>

</div>





  
 
    </div>
    )
  }
}
