import React, { Component } from 'react';


export default class Home extends Component {
	
	
	componentDidMount() {

		const text = ['Minhaz', 'Student', 'Designer', 'Web Developer'];
		const index = (text.length);
		let count = 0;
		window.onload = function typing() {


			function removeStyle() {
				document.querySelector('#typing_me').classList.remove("animate__slideInDown");
				document.querySelector('#typing_me').classList.add("animate__slideOutDown");

			}
			function type() {
				document.querySelector('#typing_me').classList.remove("animate__slideOutDown");
				document.querySelector('#typing_me').classList.add("animate__slideInDown");
				document.querySelector('#typing_me').textContent = text[count];

				setTimeout(removeStyle, 1800)

				count++; if (count === index) { count = 0 }; 
			}

			setInterval(type, 2450)


		}

	}
	render() {
		return <div id="main">
			<section id="top" className="one dark ">
				<div className="container ">

					<header>
						<div className='row'>
							<div className="col-8 col-md-8 col-sm-8 " >
								<div className='row my_intro_text'>
									<h4 className="col-8 col-md-8 col-sm-6  my_intro_text_1" > 	 Hello   I'M </h4>
									<div id='typing_me' className="  theme_color animate__animated animate__faster my_intro_text_2 "></div>

								</div>
								
								<p className='text-left pt-4 my_intro_text_3'> Together let's make something interesting <br /></p>
								 <a name="" id="" className="btn hire_me" href="#contact" role="button">Hire Me Now</a> 
							</div>

						
							<div className="hero_imge col-4 col-md-4 col-sm-4"> 
							{/* <img className="hero_imge" src="images/My_banner.jpeg" alt="" />  */}
							</div>



						</div>
						
					</header>


					

				</div>
			</section>

		</div>;
  }
}
